@mixin video-aspect-ratio($aspect-width:16, $aspect-height:9) {
    padding-bottom:percentage($aspect-height / $aspect-width);
}

@mixin video-container() {
    @include video-aspect-ratio();

    position:relative;
    display:block;
    padding-top:25px;
    height:0;

    iframe,
    object,
    embed {
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
    }
}

